@import url('https://fonts.googleapis.com/css2?family=Bungee&display=swap');

body {
    font-family: 'Bungee', cursive;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .square {
    background: #fff;
    border: 1px solid rgba(120, 67, 230, 0.8);
    float: left;
    font-family: 'Bungee', cursive;
    font-size: 48px;
    font-weight: bold;
    line-height: 100px;
    height: 100px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 100px;
    }
  
  .square:focus {
    outline: none;

  }
  
  .kbd-navigation .square:focus {
    background: #fff;
  }
  
  .game {
    display: flex;
    flex-direction: row;  
  }
  .btn-moves-info {
    width: 500px;
  }

  ol {
    display: block;
    list-style-type: decimal;
    margin-block-start: 0em;
    margin-block-end: 0em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 40px;
    margin-top : -10px;
}

  
  
  .game-info {
    margin-left: 20px;
    font-family: 'Bungee', cursive;
  }
  .status-info {
    margin-left: 15px;
    font-size: 28px;
    font-family: 'Bungee', cursive;
  }
  
  .button {
    border: 1px solid rgba(120, 67, 230, 0.8);
    color: black;
    padding: 15px 32px;
    text-align: justify;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin-top: 10px;
    font-family: 'Bungee', cursive;
    display: inline;
  }

  .title {
    text-align: center;
  }
  
  .move-box {
    display: inline;
    margin-right: 20px;
  }

  .move-box-2 {
    display: inline;
    margin-right: 63px;
  }


  @media only screen and (max-width: 900px) {
    .board-row:after {
      clear: both;
      content: "";
      display: table;
    }
    
    .square {
      background: #fff;
      border: 1px solid rgba(120, 67, 230, 0.8);
      float: left;
      font-family: 'Bungee', cursive;
      font-size: 28px;
      font-weight: bold;
      line-height: 50px;
      height: 50px;
      margin-right: -1px;
      margin-top: -1px;
      padding: 0;
      text-align: center;
      width: 50px;
      }
    
    .square:focus {
      outline: none;
  
    }
    
    .kbd-navigation .square:focus {
      background: #fff;
    }
    
    .game {
      display: flex;
      flex-direction: column;  
    }
    .btn-moves-info {
      margin-top: 20px;
      width: 100px;
    }
  
    ol {
      display: block;
      list-style-type: decimal;
      margin-block-start: 0em;
      margin-block-end: 0em;
      margin-inline-start: 0px;
      margin-inline-end: 0px;
      padding-inline-start: 40px;
      margin-top : -10px;
  }
  
    
    
    .game-info {
      margin-left: 20px;
      font-family: 'Bungee', cursive;
    }
    .status-info {
      margin-left: 15px;
      font-size: 14px;
      font-family: 'Bungee', cursive;
    }
    
    .button {
      border: 1px solid rgba(120, 67, 230, 0.8);
      color: black;
      padding: 5px 12px;
      text-align: justify;
      text-decoration: none;
      display: inline-block;
      font-size: 10px;
      margin-top: 10px;
      font-family: 'Bungee', cursive;
      display: inline;
    }
  
    .title {
      text-align: center;
    }
    
    .move-box {
      display: inline;
      margin-right: 10px;
    }
  
    .move-box-2 {
      display: inline;
      margin-right: 23px;
    }
    
  }

    